      <table class="table table-bordered">
        <tr>
          <th>.  </th>
          <th> </th>
        </tr>
        <tr
          @dblclick="deletes(item._id)"
          v-for="(item, index) in list"
          :item="item"
          :index="index"
          :key="item._id"
        >
          <template v-if="item.type=='nm'">
            <td>{{item.amount}}</td>
            <td>{{item.term}}</td>
          </template>
        </tr>
      </table>
    </div>
    <div>
      <h3>- </h3>
      <label for="amountnd">.  </label>
      <input v-model="amountnd" id="amountnd" type="text">
      <label for="termnd"> </label>
      <input v-model="termnd" id="termnd" type="text">
      <button @click.prevent="create('nd', amountnd, termnd)"></button>
      <table class="table table-bordered">
        <tr>
          <th>.  </th>
          <th> </th>
        </tr>
        <tr
