          :index="index"
          :key="item._id"
        >
          <template v-if="item.type=='nd'">
            <td>{{item.amount}}</td>
            <td>{{item.term}}</td>
          </template>
        </tr>
      </table>
    </div>
    <div>
      <h3>- </h3>
      <label for="amountom">.  </label>
      <input v-model="amountom" id="amountom" type="text">
      <label for="termom"> </label>
      <input v-model="termom" id="termom" type="text">
      <button @click.prevent="create('om', amountom, termom)"></button>
      <table class="table table-bordered">
        <tr>
          <th>.  </th>
          <th> </th>
        </tr>
        <tr
          @dblclick="deletes(item._id)"
          v-for="(item, index) in list"
          :item="item"
          :index="index"
          :key="item._id"
        >
          <template v-if="item.type=='om'">
            <td>{{item.amount}}</td>
            <td>{{item.term}}</td>
          </template>
        </tr>
      </table>
    </div>
    <div>
      <h3>   ii  i</h3>
      <label for="amounthr">.  </label>
      <input v-model="amounthr" id="amounthr" type="text">
      <label for="termhr"> </label>
      <input v-model="termhr" id="termhr" type="text">
      <button @click.prevent="create('hr', amounthr, termhr)"></button>
      <table class="table table-bordered">
        <tr>
          <th>.  </th>
